import React, {Component} from "react";
import AuthenticationService from "../authentication/services/AuthenticationService";
import RegisterForm from "../authentication/components/RegisterForm";

class RegisterScreen extends Component {
    constructor(props) {
        super(props);

        this.state = {
            email: "",
            username: "",
            password: "",
            isEmailFieldValid: true,
            isUsernameFieldValid: true,
            isPasswordFieldValid: true
        };

        this.handleSubmit = this.handleSubmit.bind(this);
        this.onChange = this.onChange.bind(this);

        this.AuthService = new AuthenticationService();
    }

    componentWillMount() {
        if (this.AuthService.loggedIn()) {
            this.props.history.replace('/');
        }
    }

    handleSubmit = async event => {
        this.AuthService.register(this.state.username, this.state.password)
            .then(res => {
                if (res.length < 50) {
                    this.setState({email: true, isUsernameFieldValid: true, isPasswordFieldValid: true})
                } else {
                    this.props.history.replace('/');
                }
            })
            .catch(err => {
                this.setState({email: false, isUsernameFieldValid: false, isPasswordFieldValid: false})
            });
    };

    onChange = async event => {
        this.setState(
            {
                [event.target.name]: event.target.value
            }
        )
    };

    render() {
        return (
            <div className="Login">
                <RegisterForm onChange={this.onChange} onSubmit={this.handleSubmit}
                              isEmailFieldValid={this.state.isEmailFieldValid}
                              isUsernameFieldValid={this.state.isUsernameFieldValid}
                              isPasswordFieldValid={this.state.isPasswordFieldValid}/>
            </div>
        );
    }
}

export default RegisterScreen;
